import {Component, OnInit, VERSION} from '@angular/core';
import {DomSanitizer} from '@angular/platform-browser';
import {Food} from '../../../models/food.model';
import {FoodAdminService} from '../../../services/food-admin.service';
import {Router} from '@angular/router';
// @ts-ignore
import {saveAs} from 'file-saver';
import {DataUrl, DOC_ORIENTATION, NgxImageCompressService, UploadResponse} from 'ngx-image-compress';


@Component({
  selector: 'app-test',
  templateUrl: './test.component.html',
  styleUrls: ['./test.component.scss'],
})
export class TestComponent {
  private selectedFile: File;
  // eslint-disable-next-line @typescript-eslint/member-ordering
  public avatarURL: any;
  private imageSource: any;
  // eslint-disable-next-line @typescript-eslint/member-ordering
  imgResultAfterResizeMax: DataUrl = '';

  constructor(
    private sanitizer: DomSanitizer,
    private foodService: FoodAdminService,
    private router: Router,
    private imageCompress: NgxImageCompressService
  ) {
  }

  // eslint-disable-next-line @typescript-eslint/member-ordering
  // @ts-ignore
  // eslint-disable-next-line @typescript-eslint/member-ordering
  food: Food = {
    title: '',
    price: 0,
    image: '',
    description: '',
    published: false,
  };

  onFileSelect(event) {
    this.selectedFile = event.target.files[0];
    console.log(this.selectedFile.name);
  }

  changeAvatar(files) {
    if (files.length === 0) {
      return;
    }

    const mimeType = files[0].type;
    if (mimeType.match(/image\/*/) == null) {
      console.log('Only images are supported.');
      return;
    }

    const reader = new FileReader();
    reader.readAsDataURL(files[0]);
    reader.onload = (_event) => {
      this.avatarURL = reader.result;
      console.log('the data is', this.avatarURL as string); // data 64 bit
    };
  }

  showImage(): void {
    console.log('testok');
    this.imageSource = this.sanitizer.bypassSecurityTrustResourceUrl(`${this.avatarURL as string}`);
    console.log('this is image', this.imageSource);

  }

  uploadAndReturnWithMaxSize() {
    return this.imageCompress.uploadAndGetImageWithMaxSize(1, true).then(
      (result: DataUrl) => {
        this.imgResultAfterResizeMax = result;
        console.log('-->',result);
      },
      (result: string) => {
        console.error(
          'The compression algorithm didn\'t succed! The best size we can do is',
          this.imageCompress.byteCount(result),
          'bytes'
        );
        this.imgResultAfterResizeMax = result;
      }
    );
  }

  saveFood(): void {
    const data = {
      title: this.food.title,
      price: this.food.price,
      image: this.imgResultAfterResizeMax,
      description: this.food.description,
      published: this.food.published,
    };

    this.foodService.create(data)
      .subscribe(
        response => {
          console.log(response);
          window.location.reload();
          //this.router.navigate(['client1/admin/listing']);

        },
        error => {
          console.log(error);
        });
  }
}
